body {
  height: 100vh;
  width: 100vw;
  background-color: #0d192b;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-family: 'Outfit', sans-serif;
}

.wrapper {
  width: 350px;
  height: 595px;
  background-color: #14253d;
  border-radius: 15px;
  -webkit-box-shadow: 0px 17px 2px 15px rgba(0, 0, 0, 0.1);
          box-shadow: 0px 17px 2px 15px rgba(0, 0, 0, 0.1);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  padding: 25px 25px 30px 25px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: left;
}

.wrapper div img {
  width: 300px;
  height: 300px;
  border-radius: 10px;
  padding: 0px;
}

.wrapper .wrapperTitle {
  color: white;
  font-weight: 600;
  font-size: 20px;
}

.wrapper .wrapperDesc {
  color: rgba(255, 255, 255, 0.5);
  font-size: 18px;
  font-weight: 300;
}

.wrapper .wrapperDivider {
  width: 100%;
}

.wrapper .wrapperDivider hr {
  border: none;
  height: .5px;
  color: rgba(255, 255, 255, 0.1);
  background-color: rgba(255, 255, 255, 0.1);
}

.wrapper .wrapperCreator {
  margin-top: -10px;
  color: white;
  font-weight: 600;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.wrapper .wrapperCreator img {
  height: 30px;
  width: 30px;
  border-style: solid;
  border-color: white;
  border-radius: 50%;
  border-width: 1px;
  margin-right: 15px;
}

.wrapper .wrapperCreator span {
  color: rgba(255, 255, 255, 0.5);
}

.wrapper .wrapperSpecs {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.wrapper .wrapperSpecs .time {
  color: rgba(255, 255, 255, 0.5);
}

.wrapper .wrapperSpecs .time img {
  height: 14px;
  width: 14px;
  padding-right: 5px;
}

.wrapper .wrapperSpecs .cost {
  color: #00fff7;
  font-weight: 600;
  letter-spacing: .025px;
}

.wrapper .wrapperSpecs .cost img {
  height: 14px;
  width: 8px;
  padding-right: 5px;
}
